<template>
  <v-container fill-height>
    <v-layout row wrap align-center fill-height>
      <v-layout row wrap>
        <!-- drawer -->
        <v-navigation-drawer
          dark
          absolute
          color="primary"
          expand-on-hover
          :mini-variant.sync="miniVariant"
        >
          <v-list
            nav
            dense
            class="py-0"
          >
            <!-- profile -->
            <v-list-item three-line>
              <v-list-item-avatar size="48" tile>
                <!-- <img src="@/assets/users/images/0x00.jpg"> -->
                <!-- <img :src="require('@/assets/users/images/0x00.jpg')"> -->
                <img :src="profilePath" style="border-radius:12%">
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>{{ user.name }}</v-list-item-title>
                <v-list-item-subtitle>Coins: {{ user.coins }}</v-list-item-subtitle>
                <v-list-item-subtitle>Score: {{ user.score }}</v-list-item-subtitle>
                <v-list-item-subtitle>Title: {{ user.title }}</v-list-item-subtitle>
              </v-list-item-content>
              <v-btn
                icon
                @click.stop="miniVariant = !miniVariant"
              >
                <v-icon>mdi-chevron-left</v-icon>
              </v-btn>
            </v-list-item>

            <v-divider></v-divider>

            <!-- actions -->
            <v-list-item-group v-model="action">
              <v-list-item
                v-for="action in actions"
                :key="action.name"
                :value="action.name"
                link
              >
                <v-list-item-avatar>
                  <v-icon>{{ action.icon }}</v-icon>
                </v-list-item-avatar>
                <v-list-item-content>
                  <v-list-item-title>{{ action.name }}</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>

            <!-- sign out -->
            <v-list-item
              @click.native="signOut"
            >
              <v-list-item-avatar>
                <v-icon>mdi-buffer</v-icon>
              </v-list-item-avatar>
              <v-list-item-content>
                <v-list-item-title>Sign Out</v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-navigation-drawer>
        <!-- main -->
        <v-flex xs1></v-flex>
        <v-flex xs11>
          <template v-if="action === 'Achievements'">
            <apps-achievements></apps-achievements>
          </template>
          <template v-else-if="action === 'Change Password'">
            <apps-change-password></apps-change-password>
          </template>
          <template v-else-if="action === 'Change Profile Image'">
            <apps-change-profile-image></apps-change-profile-image>
          </template>
          <template v-else>
            <h2 class="primary--text">Select an action from left pannel.</h2>
          </template>
        </v-flex>
        <v-flex xs12><br><br></v-flex>
      </v-layout>
    </v-layout>
  </v-container>
</template>

<script>

  import { mapGetters, mapActions } from 'vuex'

  // import Messager from '@/components/Messager'

  import Achievements from './Management/Achievements'
  import ChangePassword from './Management/ChangePassword'
  import ChangeProfileImage from './Management/ChangeProfileImage'
  
  export default {
    name: 'apps-management',
    components: {
      // appsMessager: Messager,
      appsAchievements: Achievements,
      appsChangePassword: ChangePassword,
      appsChangeProfileImage: ChangeProfileImage,
    },
    data () {
      return {
        action: 'Achievements',
        actions: [
          { name: 'Achievements', icon: 'mdi-school' },
          { name: 'Change Password', icon: 'mdi-fire' },
          { name: 'Change Profile Image', icon: 'mdi-fingerprint' },
        ],
        miniVariant: false,
      }
    },
    computed: {
      ...mapGetters({
        user: 'user',
      }),
      profilePath () {
        return require('@/assets/users/images/' + this.user.image + '.jpg')
      }
    },
    methods: {
      ...mapActions({
        setUser: 'setUser',
        signOut: 'signOut',        
      }),
    }
  }
</script>
