<template>
  <v-container fill-height>
    <v-layout row wrap align-center fill-height>
      <v-layout row wrap>
        <!--input-->    
        <v-flex xs4 offset-xs4>
          <apps-profiler></apps-profiler>
        </v-flex>
        <v-layout row wrap>
          <v-flex xs2 
            v-for="image in images"
            :key="image.name"
            :value="image.name"
          >
          <v-card 
            flat 
            hover 
            outlined 
            class="ma-4 text-center" 
            style="border-radius:4%"
            :color="name === image.name ? 'primary' : 'grey lighten-2'"
            @click.native="name = image.name"
          >
            <v-avatar size="128" tile>
              <img :src="require(`@/assets/users/images/${image.name}.jpg`)"  style="border-radius:12%">
            </v-avatar>
            </v-card>
          </v-flex>
        </v-layout>
        <v-flex xs4 offset-xs4>
          <v-btn
            block
            :loading="loading"
            :disabled="loading"
            @click.native="getUserUpdate"
            class="primary white--text"
          >
            <v-icon dark large v-html="'mdi-fingerprint'"></v-icon>
            &nbsp;&nbsp;Change Profile Image
          </v-btn>
        </v-flex>
        <v-flex xs4 offset-xs4>
          <apps-messager 
            :message="message"
          ></apps-messager>
        </v-flex>
      </v-layout>
    </v-layout>
  </v-container>
</template>

<script>

  import { mapGetters, mapActions } from 'vuex'

  import Messager from '@/components/Messager'
  import Profiler from '@/components/Profiler'

  export default {
    name: 'apps-change-profile-image',
    components: {
      appsMessager: Messager,
      appsProfiler: Profiler,
    },
    data: () => (
      {
        // main
        name: '0x00',
        images: [
          { name: '0x00', },
          { name: '0x01', },
          { name: '0x02', },
          { name: '0x03', },
          { name: '0x04', },
          { name: '0x05', },
          { name: '0x06', },
          { name: '0x07', },
          { name: '0x08', },
          { name: '0x09', },
          { name: '0x10', },
          { name: '0x11', },
          { name: '0x12', },
          { name: '0x13', },
          { name: '0x14', },
          { name: '0x15', },
          { name: '0x16', },
          { name: '0x17', },
          { name: '0x18', },
          { name: '0x19', },
          { name: '0x20', },
          { name: '0x21', },
          { name: '0x22', },
          { name: '0x23', },
          { name: '0x24', },
          { name: '0x25', },
          { name: '0x26', },
          { name: '0x27', },
          { name: '0x28', },
          { name: '0x29', },
          { name: '0x30', },
          { name: '0x31', },
          { name: '0x32', },
          { name: '0x33', },
          { name: '0x34', },
          { name: '0x35', },
        ],
        loading: false,
        message: {
          content: '', 
          status: 'success',
          outlined: true,
        }
      }
    ),
    computed: {
      ...mapGetters({
        user: 'user',
      })
    },
    methods: {
      ...mapActions({
        setUser: 'setUser',
        setUserAttr: 'setUserAttr',
        updateProperty: 'updateProperty',
      }),
      async getUserUpdate () {
        this.loading = true
        this.updateProperty({
          name: this.user.name, image: this.name
        }).then(
          // eslint-disable-next-line
          response => {
            this.loading = false
            this.setUserAttr({
              image: this.name
            })
            return response
          }
        ).then(
          // eslint-disable-next-line
          response => {
            this.message = {
              content: 'Successfully update profile image!', status: 'success', outlined: true
            }
          }
        ).catch(
          error => {
            this.loading = false
            this.message = {
              content: 'Update profile image fail: ' + error.detail, status: 'error', outlined: true
            }
          }
        )
      },
    }
  }
</script>

