<template>
  <v-container fill-height>
    <v-layout row wrap align-center fill-height>
      <v-layout row wrap>
        <!--input-->
        <template v-if="!update">
          <v-flex xs4 offset-xs4>
            <apps-profiler></apps-profiler>
          </v-flex>
          <v-flex xs4 offset-xs4>
            <v-text-field
              name="password"
              label="Enter Your New Password"
              single-line
              prepend-icon="mdi-fire"
              type="password"
              v-model="password"
              @keyup.native.enter="getUserUpdate"
            >
            <!-- :value="password" -->
            <!-- @input.native.stop="updatePassword($event.target.value)" -->
            </v-text-field>
          </v-flex>
          <v-flex xs4 offset-xs4>
            <v-text-field
              name="passwordConfirmed"
              label="Enter Your New Password Again"
              single-line
              prepend-icon="mdi-fire"
              type="password"
              v-model="passwordConfirmed"
              @keyup.native.enter="getUserUpdate"
            >
            <!-- :value="passwordConfirmed" -->
            <!-- @input.native.stop="updatePassword($event.target.value)" -->
            </v-text-field>
          </v-flex>
          <v-flex xs4 offset-xs4>
            <v-btn
              block
              :loading="loading"
              :disabled="loading"
              @click.native="getUserUpdate"
              class="primary white--text"
            >
              <v-icon dark large v-html="'mdi-fingerprint'"></v-icon>
              &nbsp;&nbsp;Change Password
            </v-btn>
          </v-flex>
        </template>
        <template v-else>
          <v-flex xs4 offset-xs4>
            <br><h3 class="primary--text text-center">Direct to sign in view in a second.</h3>
          </v-flex>
        </template>
        <v-flex xs4 offset-xs4>
          <apps-messager 
            :message="message"
          ></apps-messager>
        </v-flex>
      </v-layout>
    </v-layout>
  </v-container>
</template>

<script>

  import { mapGetters, mapActions } from 'vuex'

  import Messager from '@/components/Messager'
  import Profiler from '@/components/Profiler'

  export default {
    name: 'apps-change-password',
    components: {
      appsMessager: Messager,
      appsProfiler: Profiler,
    },
    data: () => (
      {
        // main
        update: false,
        password: '',
        passwordConfirmed: '',
        loading: false,
        message: {
          content: '', 
          status: 'success',
          outlined: true,
        }
      }
    ),
    computed: {
      ...mapGetters({
        user: 'user',
      })
    },
    methods: {
      ...mapActions({
        setUser: 'setUser',
        signOut: 'signOut',
        updatePassword: 'updatePassword',
      }),
      async getUserUpdate () {
        this.loading = true
        this.updatePassword({
          name: this.user.name, password: this.password, passwordConfirmed: this.passwordConfirmed
        }).then(
          // eslint-disable-next-line
          response => {
            this.password = ''
            this.passwordConfirmed = ''
            this.loading = false
            // return this.setUser(response)
            return response
          }
        ).then(
          // eslint-disable-next-line
          response => {
            this.update = true
            this.message = {
              content: 'Successfully update password, please sign in again!', status: 'success', outlined: true
            }
            setTimeout(() => {
              this.signOut()
            }, 3000)
          }
        ).catch(
          error => {
            this.loading = false
            this.message = {
              content: 'Update password fail: ' + error.detail, status: 'error', outlined: true
            }
          }
        )
      },
    }
  }
</script>

